				background-size: contain;
				background-repeat: no-repeat;
				margin-right: 15px;
				@include mobile {
					height: 18px; } } } }
	.column {
		height: 25vw; }
	.logo {
		background: $red2;
		display: flex;
		vertical-align: center;
		justify-content: center;
		@include mobile {
