	.image {
		background-size: cover;
		background-position: center;
		@include mobile {
			width: 100vw; } }
	.bw {
		filter: gray;
		-webkit-filter: grayscale(100%) {
	  filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale"); } }
	.med {
		font-size: .7em;
		line-height: 1.5;
		margin-top: 1em;
		@include mobile {
			margin-top: .5em;
			line-height: 1.4;
			font-size: .9em; } }
	.sm {
		font-size: 18px;
		line-height: 28px;
		margin-top: 20px; }
	.red {
		color: $red2; } }

#logos {
	img {
		margin: 20px;
		&:hover {
			opacity: .6; } }
	p:first-child, p.p1 {
		margin: 70px 0 30px;
		font-weight: 300;
		font-size: 16px;
		line-height: 1.4em; } }

