		a {
			color: #b7b7b7;
			&:hover {
				color: #6f6f6f; } }
		@include mobile {
			padding: 1rem;
			li {

 } } } }				// min-width: 33.333vw

#searchform {
	width: 40vw;
	@include mobile {
		width: 90vw; }
	margin: 0 auto;
	input {
		border: 0;
		background: transparent; }
	input[type="text"] {
		border-bottom: 1px #666 solid;
		font-size: 40px;
 }		// width: 250px
	input[type="submit"] {
		font-size: 16px;
		padding: 10px;
		border: 1px #666 solid;
		margin-left: 20px;
		border-radius: 5px; } }





.watermark {
	&:before {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 9;
		background-size: auto 90%;
		background-position: center center;
		background-repeat: no-repeat;
		background-image: url(/wp-content/themes/rueckblende/rueckblende_watermark.svg);
		opacity: 0.1;
		left: 0;
		top: 0; } }

.is-7 .watermark::before {
	width: 58.33333%; }

.slider-item .watermark {
	&:before {
		background-size: auto 65%; } }

.photo.watermark {
	&:before {
	  background-size: auto 85%;
	  background-position: center center; } }
@include mobile {
	.photo.watermark {
		&:before {
			background-size: auto 40%;
			background-position: center 10%; } } }

.single-image .photo.watermark {
	&:before {
		background-size: auto 80%;
		background-position: 50% 0; } }


.page-id-80 #events {
	.column {
		height: 25vw;
		width: 50vw;
		@include mobile {
			height: 50vw;
			width: 50vw;
			padding: 0; }
		.inner {
			display: flex;
			width: 50vw;
			@include mobile {
				width: 100vw; } }
		.square {
			height: 25vw;
			width: 25vw;
			@include mobile {
				width: 15vw;
				height: 50vw;
				border-bottom: 1px solid #e3e3e3; } }
		.text {
			@include mobile {
				width: 85vw; } } }
	.even .inner {
		flex-direction: row-reverse;
		@include mobile {
			flex-direction: row; } } }


.slider-section {
	padding-top: 2rem;
	padding-bottom: 3rem;
	width: 66vw;
	@include mobile {
		width: 100vw; }
	.year {
		font-size: 36px; }
	.slider {
		margin-top: 20px !important; } }
